@tailwind base;
@tailwind components;
@tailwind utilities;
.fader {
  width: 100%;
  position: absolute;
  height: 100%;
  z-index: 20;
  background: linear-gradient(
      270deg,
      #fff 0,
      rgba(255, 255, 255, 0.75) 6.75%,
      rgba(255, 255, 255, 0) 21.98%
    ),
    linear-gradient(
      90deg,
      #fff 0,
      rgba(255, 255, 255, 0.75) 4.89%,
      rgba(255, 255, 255, 0) 14.91%
    );
}
.swiper-custom-bullet {
  height: 2px;
  width: 2px;
  background-color: white;
}

.swiper-custom-bullet-active {
  height: 20px;
  width: 20px;
  background-color: orange;
}